* {
  margin: 0;
  padding: 0;
}
:root {
  --primary-1: #e5d4ff;
  --primary-2: #f1eaff;
}
*,
:before,
:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

@media only screen and (width <= 61.25em) {
  html {
    font-size: 50%;
  }
}

body {
  color: #615551;
  background-image: linear-gradient(to bottom right, var(--primary-2), white);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: calc(100vh - 8vw);
  font-family: Nunito Sans, sans-serif;
  font-weight: 400;
  line-height: 1.6;
}

.container {
  background-color: #fff;
  border-radius: 9px;
  grid-template:
    'head head' 10rem
    'list recipe' minmax(100rem, auto)
    / 1fr 2fr;
  max-width: 120rem;
  min-height: 117rem;
  margin: 4vw auto;
  display: grid;
  overflow: hidden;
  box-shadow: 0 2rem 6rem 0.5rem #61555133;
}

@media only screen and (width <= 78.15em) {
  .container {
    border-radius: 0;
    max-width: 100%;
    margin: 0;
  }
}

.btn--small,
.btn--small:link,
.btn--small:visited,
.btn {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-image: linear-gradient(to bottom right, var(--primary-2), #dcbfff);
  border: none;
  border-radius: 10rem;
  align-items: center;
  transition: all 0.2s;
  display: flex;
}

.btn--small:hover,
.btn:hover {
  transform: scale(1.05);
}

.btn--small:focus,
.btn:focus {
  outline: none;
}

.btn--small > :first-child,
.btn > :first-child {
  margin-right: 1rem;
}

.btn {
  padding: 1.5rem 4rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.btn svg {
  fill: currentColor;
  width: 2.25rem;
  height: 2.25rem;
}

.btn--small,
.btn--small:link,
.btn--small:visited {
  padding: 1.25rem 2.25rem;
  font-size: 1.4rem;
  font-weight: 600;
  text-decoration: none;
}

.btn--small svg,
.btn--small:link svg,
.btn--small:visited svg {
  fill: currentColor;
  width: 1.75rem;
  height: 1.75rem;
}

.btn--inline {
  color: #dcbfff;
  cursor: pointer;
  background-color: #9290c3;
  border: none;
  border-radius: 10rem;
  align-items: center;
  padding: 0.8rem 1.2rem;
  font-size: 1.3rem;
  font-weight: 600;
  transition: all 0.2s;
  display: flex;
}

.btn--inline svg {
  fill: currentColor;
  width: 1.6rem;
  height: 1.6rem;
  margin: 0 0.2rem;
}

.btn--inline span {
  margin: 0 0.4rem;
}

.btn--inline:hover {
  color: #9290c3;
  background-color: #f2efee;
}

.btn--inline:focus {
  outline: none;
}

.btn--round {
  cursor: pointer;
  background-image: linear-gradient(
    to bottom right,
    var(--primary-1),
    var(--primary-2)
  );
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 4.5rem;
  height: 4.5rem;
  transition: all 0.2s;
  display: flex;
}

.btn--round:hover {
  transform: scale(1.07);
}

.btn--round:focus {
  outline: none;
}

.btn--round svg {
  fill: #fff;
  width: 2.5rem;
  height: 2.5rem;
}

.btn--tiny {
  cursor: pointer;
  background: none;
  border: none;
  width: 2rem;
  height: 2rem;
}

.btn--tiny svg {
  fill: #dcbfff;
  width: 100%;
  height: 100%;
  transition: all 0.3s;
}

.btn--tiny:focus {
  outline: none;
}

.btn--tiny:hover svg {
  fill: #d0a2f7;
  transform: translateY(-1px);
}

.btn--tiny:active svg {
  fill: #9290c3;
  transform: translateY(0);
}

.btn--tiny:not(:last-child) {
  margin-right: 0.3rem;
}

.heading--2 {
  color: #9290c3;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 2.5rem;
  font-size: 2rem;
  font-weight: 700;
}

.link:link,
.link:visited {
  color: #9290c3;
}

.spinner {
  text-align: center;
  margin: 5rem auto;
}

.spinner svg {
  fill: #d0a2f7;
  width: 6rem;
  height: 6rem;
  animation: 2s linear infinite rotate;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.message,
.error {
  max-width: 40rem;
  margin: 0 auto;
  padding: 5rem 4rem;
  display: flex;
}

.message svg,
.error svg {
  fill: #9290c3;
  width: 3rem;
  height: 3rem;
  transform: translateY(-0.3rem);
}

.message p,
.error p {
  margin-left: 1.5rem;
  font-size: 1.8rem;
  font-weight: 600;
  line-height: 1.5;
}

.header {
  background-color: #f9f5f3;
  grid-area: head;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  height: 4.6rem;
  margin-left: 4rem;
  display: block;
}

.search {
  background-color: #fff;
  border-radius: 10rem;
  align-items: center;
  padding-left: 3rem;
  transition: all 0.3s;
  display: flex;
}

.search:focus-within {
  transform: translateY(-2px);
  box-shadow: 0 0.7rem 3rem #61555114;
}

.search__field {
  color: inherit;
  background: none;
  border: none;
  width: 30rem;
  font-family: inherit;
  font-size: 1.7rem;
}

.search__field:focus {
  outline: none;
}

.search__field::placeholder {
  color: #d3c7c3;
}

@media only screen and (width <= 61.25em) {
  .search__field {
    width: auto;
  }

  .search__field::placeholder {
    color: #fff;
  }
}

.search__btn {
  font-family: inherit;
  font-weight: 600;
}

.nav {
  align-self: stretch;
  margin-right: 2.5rem;
}

.nav__list {
  height: 100%;
  list-style: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__btn {
  color: inherit;
  text-transform: uppercase;
  cursor: pointer;
  background: none;
  border: none;
  align-items: center;
  height: 100%;
  padding: 0 1.5rem;
  font-family: inherit;
  font-size: 1.4rem;
  font-weight: 700;
  transition: all 0.3s;
  display: flex;
}

.nav__btn svg {
  fill: #9290c3;
  width: 2.4rem;
  height: 2.4rem;
  margin-right: 0.7rem;
  transform: translateY(-1px);
}

.nav__btn:focus {
  outline: none;
}

.nav__btn:hover {
  background-color: #f2efee;
}

.bookmarks {
  z-index: 10;
  visibility: hidden;
  opacity: 0;
  background-color: #fff;
  width: 40rem;
  padding: 1rem 0;
  transition: all 0.5s 0.2s;
  position: absolute;
  right: -2.5rem;
  box-shadow: 0 0.8rem 5rem 2rem #6155511a;
}

.bookmarks__list {
  list-style: none;
}

.bookmarks__field {
  cursor: pointer;
  align-items: center;
  height: 100%;
  padding: 0 4rem;
  transition: all 0.3s;
  display: flex;
}

.bookmarks__field:hover {
  background-color: #f2efee;
}

.bookmarks:hover,
.nav__btn--bookmarks:hover + .bookmarks {
  visibility: visible;
  opacity: 1;
}

.preview__link:link,
.preview__link:visited {
  border-right: 1px solid #fff;
  align-items: center;
  padding: 1.5rem 3.25rem;
  text-decoration: none;
  transition: all 0.3s;
  display: flex;
}

.preview__link:hover {
  background-color: #f9f5f3;
  transform: translateY(-2px);
}

.preview__link--active {
  background-color: #f9f5f3;
}

.preview__fig {
  backface-visibility: hidden;
  border-radius: 50%;
  flex: 0 0 5.8rem;
  height: 5.8rem;
  margin-right: 2rem;
  position: relative;
  overflow: hidden;
}

.preview__fig:before {
  content: '';
  opacity: 0.4;
  background-image: linear-gradient(to bottom right, black, #9290c3);
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.preview__fig img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: all 0.3s;
  display: block;
}

.preview__data {
  grid-template-columns: 1fr 2rem;
  align-items: center;
  row-gap: 0.1rem;
  width: 100%;
  display: grid;
}

.preview__title {
  color: rgb(182, 174, 174);
  text-transform: uppercase;
  text-overflow: ellipsis;
  white-space: nowrap;
  grid-column: 1 / -1;
  max-width: 25rem;
  font-size: 1.45rem;
  font-weight: 600;
  overflow: hidden;
}

.preview__publisher {
  color: var(--primary-1);
  text-transform: uppercase;
  font-size: 1.15rem;
  font-weight: 600;
}

.preview__user-generated {
  background-color: #eeeae8;
  border-radius: 10rem;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  margin-left: auto;
  margin-right: 1.75rem;
  display: flex;
}

.preview__user-generated svg {
  fill: #9290c3;
  width: 1.2rem;
  height: 1.2rem;
}

.search-results {
  flex-direction: column;
  padding: 3rem 0;
  display: flex;
}

.results {
  margin-bottom: 2rem;
  list-style: none;
}

.pagination {
  margin-top: auto;
  padding: 0 3.5rem;
}

.pagination:after {
  content: '';
  clear: both;
  display: table;
}

.pagination__btn--prev {
  float: left;
}

.pagination__btn--next {
  float: right;
}

.copyright {
  color: #9290c3;
  margin-top: 4rem;
  padding: 0 3.5rem;
  font-size: 1.2rem;
}

.copyright .twitter-link:link,
.copyright .twitter-link:visited {
  color: #918581;
}

.recipe {
  background-color: #f1eaff;
}

.recipe__fig {
  transform-origin: top;
  height: 30rem;
  z-index: -1;
  position: relative;
}

.recipe__fig:before {
  content: '';
  opacity: 0.323;
  background-image: linear-gradient(to bottom right, var(--primary-1), white);
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.recipe__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

.recipe__title {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  width: 60%;
  font-size: 3.25rem;
  font-weight: 700;
  line-height: 1.95;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 30%) skewY(0deg);
}

.recipe__title span {
  -webkit-box-decoration-break: clone;
  box-decoration-break: clone;
  background-image: linear-gradient(to bottom right, #d0a2f7, white);
  padding: 1.3rem 2rem;
}

@media only screen and (width <= 61.25em) {
  .recipe__title {
    width: 70%;
  }
}

.recipe__details {
  align-items: center;
  padding: 7.5rem 8rem 3.5rem;
  display: flex;
}

.recipe__info {
  text-transform: uppercase;
  align-items: center;
  font-size: 1.65rem;
  display: flex;
}

.recipe__info:not(:last-child) {
  margin-right: 4.5rem;
}

.recipe__info-icon {
  fill: #f1eaff;
  width: 2.35rem;
  height: 2.35rem;
  margin-right: 1.15rem;
}

.recipe__info-data {
  margin-right: 0.5rem;
  font-weight: 700;
}

.recipe__info-buttons {
  margin-left: 1.6rem;
  display: flex;
  transform: translateY(-1px);
}

.recipe__user-generated {
  background-color: #eeeae8;
  border-radius: 10rem;
  justify-content: center;
  align-items: center;
  width: 4rem;
  height: 4rem;
  margin-left: auto;
  margin-right: 1.75rem;
  display: flex;
}

.recipe__user-generated svg {
  fill: #d0a2f7;
  width: 2.25rem;
  height: 2.25rem;
}

.recipe__ingredients {
  background-color: #f1eaff;
  flex-direction: column;
  align-items: center;
  padding: 5rem 8rem;
  font-size: 1.6rem;
  line-height: 1.4;
  display: flex;
}

.recipe__ingredient-list {
  grid-template-columns: 1fr 1fr;
  gap: 2.5rem 3rem;
  list-style: none;
  display: grid;
}

.recipe__ingredient {
  display: flex;
}

.recipe__icon {
  fill: var(--primary-1);
  flex: none;
  width: 2rem;
  height: 2rem;
  margin-top: 0.1rem;
  margin-right: 1.1rem;
}

.recipe__quantity {
  flex: none;
  margin-right: 0.5rem;
}

.recipe__directions {
  flex-direction: column;
  align-items: center;
  padding: 5rem 10rem;
  display: flex;
}

.recipe__directions-text {
  text-align: center;
  color: #918581;
  margin-bottom: 3.5rem;
  font-size: 1.7rem;
}

.recipe__publisher {
  font-weight: 700;
}

.add-recipe-window {
  z-index: 1000;
  background-color: #fff;
  border-radius: 9px;
  width: 100rem;
  padding: 5rem 6rem;
  transition: all 0.5s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4rem 6rem #00000040;
}

.add-recipe-window .btn--close-modal {
  color: inherit;
  cursor: pointer;
  background: none;
  border: none;
  font-family: inherit;
  font-size: 3.5rem;
  position: absolute;
  top: 0.5rem;
  right: 1.6rem;
}

.overlay {
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  z-index: 100;
  background-color: #0006;
  width: 100%;
  height: 100%;
  transition: all 0.5s;
  position: fixed;
  top: 0;
  left: 0;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}

.upload {
  grid-template-columns: 1fr 1fr;
  gap: 4rem 6rem;
  display: grid;
}

.upload__column {
  grid-template-columns: 1fr 2.8fr;
  align-items: center;
  gap: 1.5rem;
  display: grid;
}

.upload__column label {
  color: inherit;
  font-size: 1.5rem;
  font-weight: 600;
}

.upload__column input {
  border: 1px solid #ddd;
  border-radius: 0.5rem;
  padding: 0.8rem 1rem;
  font-size: 1.5rem;
  transition: all 0.2s;
}

.upload__column input::placeholder {
  color: #d3c7c3;
}

.upload__column input:focus {
  background-color: #f9f5f3;
  border: 1px solid var(--primary-1);
  outline: none;
}

.upload__column button {
  grid-column: 1 / span 2;
  justify-self: center;
  margin-top: 1rem;
}

.upload__heading {
  text-transform: uppercase;
  grid-column: 1 / -1;
  margin-bottom: 1rem;
  font-size: 2.25rem;
  font-weight: 700;
}

.upload__btn {
  grid-column: 1 / -1;
  justify-self: center;
}
.recipe {
  visibility: visible;
  z-index: 120;
  margin-top: 30px;
}
.bookmarks {
  z-index: 130;
}

@media (max-width: 750px) {
  .preview {
    z-index: 900;
  }
  .header {
    display: grid;
    grid-template: repeat(2, 1fr) / repeat(3, 1fr);
    height: 700px;
    grid-template-areas:
      'a c d'
      'b b b';
    flex-flow: row;
    height: 150%;
    flex-wrap: wrap;
  }

  .header__logo {
    grid-area: a;
    padding-right: 40%;
  }
  .search {
    grid-area: b;
    height: 50px;
    margin: 20px;
  }
  .nav__list {
    gap: 50px;
  }
  .nav__btn--bookmarks {
    width: auto;
    grid-area: d;
  }
  .nav__btn--add-recipe {
    width: auto;
    grid-area: c;
  }
  .search-results {
    display: none;
  }
  .search {
    z-index: 3;
  }
  .search__field {
    width: 100%;
  }
  .recipe {
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
  }
  .recipe {
    padding-top: 10vh;
  }
  .recipe__fig {
    z-index: 5;
  }
  .recipe .message {
    position: relative;
    bottom: 150px;
  }
  .preview + .bookmarks {
    visibility: visible;
    z-index: 900 !important;
  }
  .add-recipe-window {
    scale: 0.9;
  }
  .upload {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 70vw;
    height: 90vw;
  }
}
@media (max-width: 560px) {
  .add-recipe-window {
    align-self: center;
    display: flex;
    flex-wrap: wrap;
  }
  .upload {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 90vw;
  }
  .header {
    display: grid;
    grid-template: repeat(2, 1fr) / repeat(3, 1fr);
    height: 700px;
    grid-template-areas:
      'a  c c'
      'b b b';
    flex-flow: row;
    height: 150%;
    flex-wrap: wrap;
  }
  .header__logo {
    grid-area: a;
    padding-right: 10vw;
  }
  .search {
    grid-area: b;
    height: 50px;
    margin: 20px;
  }
  .nav__list {
    gap: 20px;
    grid-area: c;
  }
  .search-results {
    display: none;
  }
  .search {
    z-index: 3;
  }
  .search__field {
    width: 100%;
  }
  .recipe {
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
  }

  .recipe__fig {
    z-index: 5;
  }

  .recipe .message {
    position: relative;
    bottom: 150px;
  }
  .search {
    width: 90vw;
  }
  .preview + .bookmarks {
    visibility: visible;
    z-index: 900 !important;
    margin-right: 0;
  }
  .add-recipe-window {
    scale: 0.9;
  }
  .upload {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 70vw;
    height: 90vw;
    scale: 0.9;
  }
  .add-recipe-window {
    scale: 0.9;
    height: 100vh;
    width: 90vw;
  }
  .upload {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 80vw;
    height: 70vh;
    scale: 0.8;
  }
}
@media (max-width: 440px) {
  .add-recipe-window {
    scale: 0.9;
    align-self: center;
    display: flex;
    flex-wrap: wrap;
  }
  .upload {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 90vw;
  }
  .header {
    display: grid;
    grid-template: repeat(2, 1fr) / repeat(3, 1fr);
    height: 700px;
    grid-template-areas:
      'a  c c'
      'b b b';
    flex-flow: row;
    height: 150%;
    flex-wrap: wrap;
  }
  .header__logo {
    grid-area: a;
    padding-right: 5vw;
  }
  .search {
    grid-area: b;
    height: 40px;
    margin: 20px;
  }
  .nav__list {
    gap: 20px;
    grid-area: c;
  }
  .search-results {
    display: none;
  }
  .search {
    z-index: 0;
  }
  .search__field {
    width: 100%;
  }

  .recipe {
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
  }
  .recipe {
    padding-top: 10vh;
  }
  .recipe__fig {
    z-index: 5;
  }
  .recipe .message {
    position: relative;
    bottom: 150px;
  }
  .search {
    width: 90vw;
  }
  .bookmarks {
    z-index: 12;
  }
  .preview {
    overflow: visible;
  }
  .preview:hover + :active {
    overflow: visible;
    position: fixed;
  }
  .preview + .bookmarks {
    visibility: visible;
    z-index: 900 !important;
  }
  .add-recipe-window {
    scale: 0.9;
    height: 100vh;
    width: 90vw;
  }
  .upload {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 80vw;
    height: 70vh;
    scale: 0.8;
  }
}
@media (max-width: 940px) {
  .add-recipe-window {
    scale: 0.9;
    height: 100vh;
    width: 90vw;
  }
  .upload {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 80vw;
    height: 70vh;
    scale: 0.8;
  }
}
